import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';

class Square extends React.Component {
  constructor(props)
  {
    super(props);
    this.state={
      value: null,
    };
  }
  render() {
    return (
      <button 
        className="square" 
        onClick={()=>this.props.onClick()}>
        {this.props.value}
      </button>
    );
  }
}

class Board extends React.Component {
  constructor(props)
  {
    super(props);
    this.state={
      squares: [
        null, null, null,
        null, null, null,
        null, null, null,
      ],
      xIsNext: true
    }
  }
  handleClick(i){
    const squares=this.state.squares.slice();
    if(calculateWinner(squares) || squares[i])
    {
      return;
    }
    squares[i]=this.state.xIsNext?'壮':'爸';
    this.setState({
        squares: squares,
        xIsNext: !this.state.xIsNext});    
  }
  renderSquare(i) {
    return <Square 
      value={this.state.squares[i]}
      onClick={()=>this.handleClick(i)}
      />;
  }


  render() {
    const winer=calculateWinner(this.state.squares);
    let status; 
    let icon; 
    if(winer) {
      status='恭喜赢啦: '+ winer;
      icon="./game/timg.gif";
    }
    else
    {
      status= '下一个: '+ (this.state.xIsNext?'壮':'爸');
      icon="./game/selectReqType.jpg";
    }

    return (
      <div>
         <div className="title">壮壮和爸爸的井字棋游戏大战</div>
         <audio src="./game/happy.mp3" controls="controls" preload id="music1" autoplay="autoplay" hidden ></audio>
        <div className="status"><img src={icon} width="30" alt='next' align="left"/>{status}<span className="toolbar" id="restartBtn">重新开始</span></div>
        
        <div className="board-row">
          {this.renderSquare(0)}
          {this.renderSquare(1)}
          {this.renderSquare(2)}
        </div>
        <div className="board-row">
          {this.renderSquare(3)}
          {this.renderSquare(4)}
          {this.renderSquare(5)}
        </div>
        <div className="board-row">
          {this.renderSquare(6)}
          {this.renderSquare(7)}
          {this.renderSquare(8)}
        </div>
      </div>
    );
  }
}


class Game extends React.Component
{
    constructor(props)
    {
      super(props);
      this.state={
        history:[{
          squares: [
            null, null, null,
            null, null, null,
            null, null, null,
          ]
        }],
        xIsNext: true
      }
    }

    render(){
      return (
        <div className="game">
          <div className="game-border">
            <Board/>
          </div>
          <div className="game-info">
            <div></div>
            
          </div>
        </div>)
    }
}

// ========================================

ReactDOM.render(
  <Game />,
  document.getElementById('root')
);

function calculateWinner(squares) {
  const lines = [
    [0, 1, 2],
    [3, 4, 5],
    [6, 7, 8],
    [0, 3, 6],
    [1, 4, 7],
    [2, 5, 8],
    [0, 4, 8],
    [2, 4, 6],
  ];
  for (let i = 0; i < lines.length; i++) {
    const [a, b, c] = lines[i];
    if (squares[a] && squares[a] === squares[b] && squares[a] === squares[c]) {
      return squares[a];
    }
  }
  return null;
}

